@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@300;400;500;700;900&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{
  text-decoration: none;
  font-family: 'Roboto', sans-serif;
}
.navbar{
    background-color: blueviolet;
}
.home-info h3{
    font-size: 68px;
    color: #fff;
    font-weight: 900;
    display: inline-block;
    padding: 10px;
    background-color: blueviolet;
}

.home-info p{
    font-size: 24px;
    font-weight: 600;
    color: #fff;
}
.populer-courses{
    padding-top: 40px;
    text-align: center;
}
.find-more{
    text-decoration: none;
}
.instructors{
    text-align: center;
    padding-top: 40px;
}
.about-text{
    padding: 20px 30px;
}
.about-text p{
    font-size: 20px;
}
.certificates h2{
    padding-left: 2%;
}
.footer{
    background-color: blueviolet;
    color: #fff;
    text-align: center;
    font-size: 14px;
    width: 100vw;
    box-sizing: border-box;
    padding: 2%;
    overflow-x: hidden;
}
.details{
    display: flex;
    justify-content: center;
    align-items: center;
}
.details img{
    width: 300px;
}

.social-link{
    list-style: none;
    display: flex;
}
.social-link li{
    padding-right: 10%;
}
.about{
    text-align: left;
}
.links li{
    color: #fff;
}
